<template>
  <div class="MovieList">
    <header>
      <h2>전체 영화</h2>
    </header>
    <!-- MovieCards -->
    <div class="popular-list row row-cols-1 row-cols-md-5 gy-3">
      <!-- v-for(movie,idx) in movies : 영화 순위 줄 세우기 -->
      <MovieCard
        v-for="(movie, idx) in movies" 
        :key="idx"
        :movie="movie"/>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import MovieCard from '@/components/movies/MovieCard'
export default {
  name:'MovieList',
  components:{
    MovieCard,
  },
  methods:{
    setToekn(){
      const token = localStorage.getItem('jwt')
      const config = {
        Authorization: `JWT ${token}`
      }
      return config
    },
  },
  computed :{
    ...mapGetters([
      'movies',
    ])
  },
    created() {
    this.$store.dispatch('getMovies', this.setToken())
    
  },
}
</script>

<style>
 .MovieList h2{
      text-align: center;
   color:black;
 }
</style>