<!-- v 0.8 수정내용 : 흰 배경 용 헤더(Base_Header) 추가. home.vue를 제외한 모든 파일에 추가해야함
  지역 선택 박스 v-select로 수정 더 예뻐짐  -->
<template>
  <div class = "accounts_form">
    <div class="authForm join">
      <h2>회원가입</h2>
      <v-form class="signupInput" method="POST" @submit.prevent="submit">
        <v-container>
        <v-row justify="center">
            <v-text-field class="form-control-join" id="email" v-model="email" type="email" label="* 이메일" maxlength="20" clearable required></v-text-field><br><br><br>
            <v-text-field class="form-control-join" id="password" v-model="password" type="password" label="* 비밀번호" minlength="8" maxlength="16" hint="8~16자, 영문과 숫자의 조합으로 입력해주세요." clearable required></v-text-field><br><br><br>
            <v-text-field class="form-control-join" id="passwordConfirmation" v-model="passwordConfirmation" type="password" label="* 비밀번호 확인" hint="비밀번호를 다시 입력해주세요" min-length="8" maxlength="16" clearable required></v-text-field><br><br><br>
            <v-text-field class="form-control-join" id="username" v-model="username" type="username" label="* 이름" maxlength="10" clearable required></v-text-field><br><br><br>
            <v-text-field class="form-control-join" id="tel" v-model="tel" type="tel" label="* 휴대폰 번호" maxlength="13" clearable required></v-text-field><br><br><br>

        <label><span>마케팅 수신 동의  :</span><input type="radio" name="marketing" value="Y" checked="checked">Y<input type="radio" name="marketing" value="N">N</label><br><br>
        <v-text-field class="form-control-join" id="signumber" v-model="signumber" type="text" label="* 주민번호 7자리" minlength="7" maxlength="7" clearable required></v-text-field><br><br><br><br><br>
        <v-select v-model="local" label="지역 (시/도)" :items="localList" item-text="name" item-value="value"></v-select>
        <v-text-field class="form-control-join" id="adress" v-model="adress" type="text" label="상세 주소 (선택사항)" :counter="45" clearable></v-text-field>
        </v-row>
        </v-container>
      </v-form>
        <button @click="signup" class="btn submitBtn">회원가입</button>
    </div>
  </div>
</template>
<script>
import api from '@/api/index'

export default {
  name: 'Signup',
  data() {
    return {
      localList: [
        {name:'서울', value:'서울'}, {name:'경기', value:'경기'}, {name:'인천', value:'인천'}, {name:'강원', value:'강원'},
        {name:'대전', value:'대전'}, {name:'충청', value:'충청'}, {name:'대구', value:'대구'}, {name:'부산', value:'부산'},
        {name:'울산', value:'울산'}, {name:'경상', value:'경상'}, {name:'광주', value:'광주'}, {name:'전라', value:'전라'},
        {name:'제주', value:'제주'}
      ],
        username: null,
        password: null,
        passwordConfirmation: null,
        email: null,
        tel:null,
        signumber:null,
        adress:null,


    }
  },
methods: {
    submit() {
      this.loading = true
      this.$vs.loading()
      const { email, name, password, password2 } = this
      if (password !== password2) {
        alert('비밀번호가 일치하지 않습니다.')
        return
      }
      api
        .post('/api/signup', {
          email,
          name,
          password
        })
        .then((res) => {
          this.loading = false
          this.$vs.loading.close()
          const { data } = res
          // 회원가입 성공 시
          if (data.result === 1) {
            this.$vs.notify({
              title: '회원가입 완료',
              text: data.message,
              color: 'success'
            })
            this.$router.push('/login')
          }
          // 회원가입 실패 시
          if (data.result === 0) {
            // alert(data.message)
            this.$vs.notify({
              title: '회원가입 실패',
              text: data.message,
              color: 'danger'
            })
          }
        })
        .catch((err) => {
          this.loading = false
          this.$vs.loading.close()
          console.log(err)
          this.$vs.notify({
            title: '회원가입 실패',
            text: '이미 등록된 이메일 주소입니다.',
            color: 'danger'
          })
        })
    }
  }
}
</script>

<style>
  @import '../../assets/styles/common.css';
</style>