<template>
  <div class="col-mb-6 movie-item"> <!--세로로 6-->
    <v-dialog
      v-model="dialog"
      fullscreen 
      hide-overlay
      transition="dialog-bottom-transition"
    >
    <!--v-slot:activator="{ on, attrs }" 는 activator 이름을 가진 슬롯에서 on, attrs 슬롯 속성(slot props)을 가져온다는 의미이다.-->
      <template v-slot:activator="{ on, attrs }">
      <v-img
        :src="imgSrc" alt ="포스터 없음"
        v-bind="attrs"
        v-on="on"
      >
      </v-img>
    </template>
    <!--포스터 클릭시-->
    <div class="movie-detail-card">
      <div class="movie-detail-toolbar">
        <v-btn
          icon
          dark
          @click="dialog = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <img id="logo-image" src="@/assets/images/logo_black.png"/>
      </div>
      <div class="movie-detail-body">
        <div class="movie-detail-poster">
          <img :src="imgSrc" alt="포스터 없음">
        </div>
        <div class="movie-detail-info">
          <!-- info header -->
          <div class="movie-detail-upper">
            <div class="movie-detail-info-header">
              <div class="movie-detail-info-header-left">
                <div class="movie-detail-title">
                  {{ movie.mov_name }}
                </div>
                <div
                v-if="movie.release_date"
                class="movie-release-date">
                  개봉  :  {{ movie.release_date }}
                </div>
                <div
                v-if="movie.age">
                  상영 등급: {{ movie.grade }}
                </div>
              </div>
              <div class="movie-detail-info-header-right">
                <div class="movie-mov_star">
                  {{ movie.mov_star }} <!--별점 평균...-->
                </div>
                 <img id="movie-star" src="@/assets/images/star.png">
              </div>
            </div>
            <!-- review -->
            <div class="movie-detail-overview-header">
              한줄평
            </div>
            <hr>
            <div
              v-if="movie.mov_review"
              class="movie-detail-overview-body">
              {{ movie.mov_review }}
            </div>
 
          </div>
     
        </div>
      </div>
    </div>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: 'MovieCard',
  components: {
    
  },
  props: {
    movie: {
      type: Object,
      required: true
    },
  },
  data() {
    return {
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
    }
  },
  computed: {
    imgSrc: function () {
      return "@/assets/images/"+"poster1.jpg"
       //"https://image.tmdb.org/t/p/original" + this.movie.poster_path
    },
  },
}
</script>

