<template>
  <div class="accounts_form">
    <div class="authForm">
    <h2>로그인</h2>
    <div class="loginInput">
      <form method="POST" @submit.prevent="submit">
        <input class="form-control" id="username" type="email" v-model="email" placeholder="이메일" required/>
        <input
          class="form-control"
          id="password"
          type="password"
          placeholder="비밀번호"
          v-model="password"
          required
        />
        <div class="loginAddOn">
        <a :href="nonmemLink" >비회원 예매하기</a>
        <br><br>
        <a :href="nonmemLinkCheck">비회원 예매확인</a>
        <br><br>
        <span>ONPELX 회원이 아닌가요?</span> <a :href="signupLink">회원가입</a>
        </div>
        <button type="submit"  class="btn submitBtn"
          >로그인</button
        >
      </form>
    </div>
    </div>
  </div>
</template>

<script>
import api from '@/api'
export default {
  name: 'Login',
  data() {
    return {
      signupLink: '/accounts/signup/',
      nonmemLink :'/accounts/Nonmember/',
      nonmemLinkCheck : '/accounts/NonmemberCheck/',
      email: '',
      password: '',
      token: ''
    }
  },
  methods: {
    submit() {
      const { email, password } = this
      api
        .post('/api/login', {
          email,
          password
        })
        .then((res) => {
          const { data } = res
          if (data.result === 1) {
            console.log('프론트엔드 로그인 성공')
            this.$vs.notify({
              title: '로그인 성공',
              text: data.message,
              color: 'success'
            })
            this.$store.commit('login', data)
            // this.$cookie.set('accessToken', data.token, 7)
            this.$router.push('/')
          }
          if (data.result === 0) {
            console.log('프론트엔드 로그인 실패')
            this.$vs.notify({
              title: '로그인 실패',
              text: data.message,
              color: 'danger'
            })
          }
        })
        .catch((err) => {
          console.log(err)
          console.log('프론트엔드 로그인 실패')
          this.$vs.notify({
            title: '로그인 실패',
            text: '비밀번호를 다시 입력하세요.' || err,
            color: 'danger'
          })
        })
    }
  }
}
</script>

<style>
 @import '../../assets/styles/common.css';

</style>